import React from 'react';

export default class Plus extends React.PureComponent{
 
    render(){
        //console.log('Plus')
        const {plus}=this.props
        return(
            plus ?
                plus.length===0 ?
                    <p className="plus">{'  '}</p> :
                plus.map((e,i)=>(
                    e ? <p key={i} className="plus">{e}</p> : null
                )) :
                <p>plus</p>
        )
    }
}



